import React, { ReactNode, useState } from "react";
import * as auth from "auth-provider";
import { User } from "types/user";
import { http } from "utils/http";
import { useMound } from "screens/project-list";
import { useAsync } from "utils/use-async";
import { FullPageErrorFallback, FullPageLoading } from "components/lab";
import { DevTools } from "jira-dev-tool";

const AuthContext =
  React.createContext<
    | {
        user: null | User;
        login: (form: AuthForm) => Promise<void>;
        register: (form: AuthForm) => Promise<void>;
        logout: () => Promise<void>;
      }
    | undefined
  >(undefined);

interface AuthForm {
  username: string;
  password: string;
}

const bootstrapUser = async () => {
  let user = null;
  const token = auth.getToken();
  if (token) {
    const data = await http("me", { token });
    user = data.user;
  }
  return user;
};

AuthContext.displayName = "AuthContext";

export const AuthProvider = ({ children }: { children: ReactNode }) => {
  // const [user, setUser] = useState<User | null>(null);
  const {
    data: user,
    error,
    isLoading,
    isIdle,
    isError,
    run,
    setData: setUser,
  } = useAsync<User | null>();
  const login = (form: AuthForm) => auth.login(form).then(setUser);
  const register = (form: AuthForm) => auth.register(form).then(setUser);
  const logout = () =>
    auth.logout().then(() => {
      setUser(null);
    });
  useMound(() => {
    // bootstrapUser().then(setUser);
    run(bootstrapUser());
  });
  if (isIdle || isLoading) return <FullPageLoading></FullPageLoading>;
  if (isError) {
    return (
      <div>
        <DevTools></DevTools>
        <FullPageErrorFallback error={error}></FullPageErrorFallback>
      </div>
    );
  }
  return (
    <AuthContext.Provider
      children={children}
      value={{ user, login, register, logout }}
    />
  );
};

export const useAuth = () => {
  const context = React.useContext(AuthContext); //hook内容
  if (!context) {
    throw new Error("useAuth必须在AuthProvider中使用");
  }
  return context;
};
