import styled from "@emotion/styled";
import { ButtonNoPadding, Row } from "components/lab";
import { useAuth } from "context/auth-context";
import { ReactComponent as SoftwareLogo } from "assets/software-logo.svg";
import React, { useState } from "react";
import { ProjectListTwo } from "screens/project-list-2";
import { Navigate, Route, Routes } from "react-router";
import { BrowserRouter as Router } from "react-router-dom";
import { ProjectScreen } from "screens/project";
import { Button } from "antd";
import { resetRoute } from "utils";
import { ProjectModal } from "screens/project-list-2/project-modal";
import { ProjectPopover } from "components/project-popover";

const Header = (props: { projectButton: JSX.Element }) => {
  const { logout } = useAuth();
  return (
    <PageHeader>
      <HeaderLeft gap={true}>
        <Button type={"link"} onClick={resetRoute}>
          <SoftwareLogo
            width={"18rem"}
            color={"rgb(38,132,255)"}
          ></SoftwareLogo>
        </Button>
        {/* <h2>项目</h2> */}
        <ProjectPopover {...props}></ProjectPopover>
        <span>用户</span>
      </HeaderLeft>
      <HeaderRight>
        <button onClick={logout}>登出</button>
      </HeaderRight>
    </PageHeader>
  );
};

export const AuthenticatedApp = () => {
  const [projectModalOpen, setProjectModalOpen] = useState(false);
  return (
    <div>
      <Header
        projectButton={
          <ButtonNoPadding
            type={"link"}
            onClick={() => {
              setProjectModalOpen(true);
            }}
          >
            创建项目
          </ButtonNoPadding>
        }
      ></Header>
      <Main>
        {/* <ProjectListTwo /> */}
        <Router>
          <Routes>
            <Route
              path={"/projects"}
              element={
                <ProjectListTwo
                  projectButton={
                    <ButtonNoPadding
                      type={"link"}
                      onClick={() => {
                        setProjectModalOpen(true);
                      }}
                    >
                      编辑
                    </ButtonNoPadding>
                  }
                />
              }
            ></Route>
            <Route
              path={"/projects/:projectId/*"}
              element={<ProjectScreen />}
            ></Route>
            <Navigate to={"/projects"} />
          </Routes>
        </Router>
      </Main>
      <ProjectModal
        projectModalOpen={projectModalOpen}
        onClose={() => setProjectModalOpen(false)}
      ></ProjectModal>
    </div>
  );
};

const PageHeader = styled(Row)`
  height: 6rem;
  justify-content: space-between;
  padding: 3.2rem;
  box-shadow: 0 0 5px 0 rgba(0, 0, 0, 0.1);
`;
const Main = styled.main`
  height: calc(100vh - 6rem);
  margin-top: 2rem;
`;
const HeaderLeft = styled(Row)``;
const HeaderRight = styled.div``;
