import { Dropdown, Menu, Table, TableProps } from "antd";
import { ButtonNoPadding } from "components/lab";
import { Pin } from "components/pin";
import dayjs from "dayjs";
import React from "react";
// react-router(管理路由状态) 和 react-router-dom(消费结果) 的关系  类似于 react 和 react-dom/react-native
import { Link } from "react-router-dom";
import { Project } from "types/project";
import { User } from "types/user";
import { useEditProject } from "utils/project";
interface ListProps extends TableProps<Project> {
  users: User[];
  refresh?: () => void;
  projectButton: JSX.Element;
}

export const List = ({ users, ...props }: ListProps) => {
  const { mutate } = useEditProject();
  const pinProject = (id: number, pin: boolean) =>
    mutate({ id, pin }).then(props.refresh);
  //等同于上方,  柯里化风格  因为 先有id 在有 pin
  // const pinProject = (id: number) => (pin: boolean) => mutate({ id, pin });

  const columns = [
    {
      title: <Pin checked={true} disabled={true}></Pin>,
      render(value: any, project: any) {
        return (
          <Pin
            checked={project.pin}
            onCheckChange={(pin) => {
              pinProject(project.id, pin);
            }}
          ></Pin>
        );
      },
    },
    {
      key: "1",
      title: "名称",
      render(value: any, project: any) {
        return <Link to={String(project.id)}>{project.name}</Link>;
      },
    },
    {
      key: "2",
      title: "部门",
      dataIndex: "organization",
    },
    {
      key: "3",
      title: "负责人",
      render: (text: any, record: any) => {
        return (
          <span>
            {users.find((v) => v.id === record.personId)?.name || "未知"}
          </span>
        );
      },
    },
    {
      title: "创建时间",
      render(value: any, project: any) {
        return (
          <span>
            {project.created
              ? dayjs(project.created).format("YYYY-MM-DD")
              : "无"}
          </span>
        );
      },
    },
    {
      render(value: any, project: any) {
        return (
          <Dropdown
            overlay={
              <Menu>
                <Menu.Item key={"edit"}>
                  {/* <ButtonNoPadding
                    type={"link"}
                    onClick={() => {
                      props.setProjectModalOpen(true);
                    }}
                  >
                    编辑
                  </ButtonNoPadding> */}
                  {props.projectButton}
                </Menu.Item>
              </Menu>
            }
          >
            <ButtonNoPadding type={"link"}>...</ButtonNoPadding>
          </Dropdown>
        );
      },
    },
  ];
  return (
    <div>
      <Table pagination={false} columns={columns} {...props}></Table>
    </div>
  );
};
