import { useEffect, useRef } from "react";
export const isFalsy = (value: unknown) => (value === 0 ? false : !value);
export const cleanObject = (object: { [propName: string]: unknown }) => {
  // 不要直接修改  会造成污染
  let result = { ...object };
  Object.keys(result).forEach((key) => {
    const value = result[key];
    if (isFalsy(value)) {
      //是否为false
      delete result[key];
    }
  });
  return result;
};

export const useDocumentTitle = (
  title: string,
  keepOnUnmount: boolean = true
) => {
  // const oldTit = document.title;
  const oldTit = useRef(document.title).current;
  useEffect(() => {
    document.title = title;
  }, [title]);
  useEffect(() => {
    return () => {
      if (!keepOnUnmount) document.title = oldTit;
    };
  });
};

export const resetRoute = () => (window.location.href = window.location.origin);

// 返回组件的挂在状态,如果还没挂在或者已经卸载,返回false,反之返回true
export const useMountedRef = () => {
  const mountedRef = useRef(false);
  useEffect(() => {
    mountedRef.current = true;
    return () => {
      mountedRef.current = false;
    };
  });
  return mountedRef;
};
